ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #93947e;
}

li {
  float: left;
}

li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

li a:hover:not(.active) {
  background-color: #454637;
  color: black;
}

.active {
  background-color: #454637;
}

button{
  background-color: #454637;
color: white;
padding-left: 10px;
padding-right: 10px;
padding-top: 4px;
padding-bottom: 4px;
border-radius: 25%;
}
button:hover{
  background-color: #93947e;
  border-radius: 1;
}